
namespace Juice {

    export namespace Internals {

        export function isPrimitive(value: any): boolean {

            if (value === null)
                return false;

            var type = typeof value;
            switch (type) {
                case "string":
                case "number":
                case "boolean":
                    return true;
                default:
                    return false;
            }
        }
    }
}