
namespace Juice {

    export interface ClassInfoBase {
        baseClass: any; // TODO
        className: string;
        isAbstract?: boolean;
    }

    export interface ComponentInfoBase extends ClassInfoBase {
        tagName?: string;
    }

    export interface ClassInfo<T> extends ClassInfoBase {
        classConstructor: new (...args: any[]) => T;
    }

    export interface ComponentInfo<T> extends ComponentInfoBase {
        classConstructor: new (...args: any[]) => T;
    }

    export interface ComponentsFilterOptions {
        baseClass?: any;
        className?: RegExp;
        tagName?: RegExp;
        classConstructor?: (...args: any[]) => any;
        extendClass?: any;
    }

    export class Builder {

        // private static _classes: { [name: string]: ClassInfoBase } = {};
        // private static _components: { [name: string]: ComponentInfoBase } = {};
        private static _classes = new Map<string, ClassInfoBase>();
        private static _components = new Map<string, ComponentInfoBase>();

        private static registerClass(info: ClassInfoBase) {
            if (this._classes.has(info.className)) {
                console.log(info.className + " already defined");
                return;
            }
            this._classes.set(info.className, info);
        }

        private static registerComponent(info: ComponentInfoBase) {

            if (this._components.has(info.className)) {
                console.log(info.className + " already defined");
                return;
            }
            this._components.set(info.className, info);


            // var lowerTagName = info.tagName.toLowerCase();
            // if (this._components.has(lowerTagName)) {
            //     console.log(info.tagName + " already defined");
            //     return;
            // }
            // this._components.set(lowerTagName, info);

            //window.customElements.define(info.tagName, (info as any).classConstructor);
        }

        private static classExtendsOrIsClass(componentInfo: ComponentInfoBase, testClass: any) {
            if((componentInfo as any).classConstructor === testClass){
                return true;
            }
            return (componentInfo as any).classConstructor.prototype instanceof testClass;
        }

        public static defineComponent<T>(info: ComponentInfo<T>) {
            this.registerComponent(info);
            Object.defineProperty(info.classConstructor, "baseClass", { value: info.baseClass });
            Object.defineProperty(info.classConstructor, "className", { value: info.className });
            if(info.tagName) {
                Object.defineProperty(info.classConstructor, "tagName", { value: info.tagName });
            }
        }

        public static defineClass<T>(info: ClassInfo<T>) {
            this.registerClass(info);
            Object.defineProperty(info.classConstructor, "className", { value: info.className });
            Object.defineProperty(info.classConstructor, "baseClass", { value: info.baseClass });
        }

        public static applyBehaviours(type: any, behaviourTypes: any[]) {
            behaviourTypes.forEach(baseCtor => {
                Object.getOwnPropertyNames(baseCtor.prototype).forEach(name => {
                    Object.defineProperty(type.prototype, name, Object.getOwnPropertyDescriptor(baseCtor.prototype, name));
                });
            });
        }

        public static getComponents(filterOptions? : ComponentsFilterOptions): Iterable<ComponentInfoBase> {
            if (!filterOptions) {
                return this._components.values();
            }
            else {
                let filterHandler = (v: ComponentInfoBase) => {
                    if (filterOptions.extendClass && !this.classExtendsOrIsClass(v, filterOptions.extendClass)){
                        return false;
                    }
                    if (filterOptions.baseClass && filterOptions.baseClass !== v.baseClass){
                        return false;
                    }
                    if (filterOptions.className && !filterOptions.className.test(v.className)){
                        return false;
                    }
                    if (filterOptions.tagName && !filterOptions.tagName.test(v.tagName)){
                        return false;
                    }
                    if (filterOptions.classConstructor) {
                        if("classConstructor" in (v as any)) {
                            let component = v as any;
                            if (filterOptions.classConstructor !== component.classConstructor){
                                return false;
                            }
                        } else {
                            return false;
                        }
                    }
                    return true;
                };
                return Array.from(this._components.values()).filter(filterHandler);
            }
        }
    }
}