
namespace Juice {
    
    export namespace Internals {

        export var templateDirPath = "/assets/templates/";

        export function getTemplateFile(relativePath: string): Promise<Document> {
            let path = templateDirPath + relativePath;
            return new Promise<Document>((resolve, reject) => {
                fetch(path)
                    .then((response) => {
                        return response.text();
                    })
                    .then((content) => {
                        // Convert the HTML string into a document object
                        var parser = new DOMParser();
                        var doc = parser.parseFromString(content, 'text/html');
                        resolve(doc);
                    })
                    .catch(err => {
                        reject(err);
                    });
            });
        }
    }
}
