/// <reference path="ItemsControl.ts" />
/// <reference path="HeaderedControl.ts" />


namespace Juice {

    export class HeaderedItemsControl extends UIElement {
        constructor(template?: TemplateSource) {
            super(template);
        }

        protected initializeTemplate(templatedElement: TemplatedElement) {
            super.initializeTemplate(templatedElement);
            UIElementBehaviour.initialize(HeaderedControlBehaviour, this);
            UIElementBehaviour.initialize(ItemsControlBehaviour, this);
            this._setHeaderElement(templatedElement.getPart<HTMLElement>("header").element);
        }
    }

    export interface HeaderedItemsControl extends HeaderedControlBehaviour, ItemsControlBehaviour {
        new(template?: string | HTMLTemplateElement | HTMLElement): HeaderedItemsControl;
    }

    Builder.applyBehaviours(HeaderedItemsControl, [HeaderedControlBehaviour, ItemsControlBehaviour]);

    Builder.defineComponent({
        baseClass: UIElement,
        classConstructor: HeaderedItemsControl,
        className: "Juice.HeaderedItemsControl",
        tagName: "jui-headered-items-control"
    });
}