
namespace Juice {

    export namespace Internals {

        interface InstanceReference {
            instance: any;
            element: Element;
        }

        const _uids: Array<string> = [];
        const _uidsInstances: Map<string, InstanceReference> = new Map<string, InstanceReference>();

        function s4() {
            return Math.floor((1 + Math.random()) * 0x10000)
                .toString(16)
                .substring(1);
        }


        export function getFreeUid() {
            var uid;
            do {
                uid = s4() + s4();
            } while (_uids.indexOf(uid) >= 0);
            return uid;
        }

        export function registerElement(uid: string, instance: any, element: Element) {
            _uidsInstances.set(uid, { instance, element });
        }

        export function setContent(element: HTMLElement, content: Content) {
            if (typeof content === "undefined" || content == null) {
                element.innerHTML = "";
                return;
            }

            if (Internals.isPrimitive(content)) {
                if (typeof content === "string" && /^\s*<\w+/.test(content)) {
                    element.innerHTML = content;
                } else {
                    element.innerText = content.toString();
                }
            }
            else if (content instanceof UIElement) {
                // TODO set parent
                element.appendChild(content.htmlElement);
            }
            else {
                if (content instanceof HTMLElement) {
                    element.appendChild(content);
                }
                else if (content instanceof NodeList) {
                    for (let child of content) {
                        element.appendChild(child);
                    }
                }
                else {
                    element.innerText = content.toString();
                }
            }
        }
    }
}