/// <reference path="Builder.ts" />
/// <reference path="HeaderedContentControl.ts" />

namespace Juice {

    export interface AccordionStateChangedArgs {
        isExpanded: boolean;
        isFirstExpansion: boolean;
    }

    export class Accordion extends HeaderedContentControl {

        protected static readonly DefaultAccordionStyles = `
    .jui-accordion {
    
    }
    
    .jui-accordion-header {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        /*border-top-left-radius: 3px;
        border-top-right-radius: 3px;
        border: 1px solid #ccc;
        background: #eee;
        padding-top: 4px;
        padding-left: 6px;
        padding-right: 3ch;
        padding-bottom: 5px;*/
        position: relative;
        user-select: none;
        cursor: pointer;
    }
    
    .jui-accordion-symbol {
        position: absolute;
        right: 1ch;
        top: 50%;
        transform: translateY(-50%);
        /*color: #777;*/
    }

    .jui-accordion-symbol > .expanded {
        display: none;
    }

    .jui-accordion-symbol > .collapsed {
        display: inline;
    }
    
    .jui-accordion-symbol.expanded > .expanded {
        display: inline;
    }
    
    .jui-accordion-symbol.expanded > .collapsed {
        display: none;
    }
    
    .jui-accordion-container {
        display: none;
        /*padding: 6px;
        border-top: 0;
        border-left: 1px solid #ccc;
        border-right: 1px solid #ccc;
        border-bottom: 1px solid #ccc;*/
        content: ' ';
    }`;

        protected static readonly DefaultAccordionHtmlTemplate = `
    <template template-class="Juice.Accordion">
        <div class="jui-accordion">
            <div template-part="accordionHeader" class="jui-accordion-header">
                <div template-part="accordionHead" class="jui-accordion-head"><span template-part="header"></span></div>
                <div template-part="accordionSymbol" class="jui-accordion-symbol"><i class="fa fa-chevron-down collapsed"></i><i class="fa fa-chevron-up expanded"></i></div>
            </div>
            <div template-part="accordionContainer" class="jui-accordion-container">
                <div>text from template</div>
                <div template-part="content"></div>
            </div>
        </div>
    </template>`;

        private static readonly DefaultAccordionTemplate = `<style>\n${Accordion.DefaultAccordionStyles}\n</style>\n${Accordion.DefaultAccordionHtmlTemplate}`;

        private _isExpanded = false;
        private _neverExpanded = true;

        private _part_accordionHeader: HTMLElement;
        private _part_accordionHead: HTMLElement;
        private _part_accordionSymbol: HTMLElement;
        private _part_accordionContainer: HTMLElement;

        constructor(template?: string | HTMLTemplateElement | HTMLElement) {
            super(template || Accordion.DefaultAccordionTemplate);
            this.onStateChanged = new EventSet<Accordion, AccordionStateChangedArgs>(this.events, "onStateChanged", this);
            //this.registerApplyTemplateListener(this.initialize);
        }

        protected initializeTemplate(templatedElement: TemplatedElement) {
            super.initializeTemplate(templatedElement);

            // get template parts
            this._part_accordionHeader = templatedElement.withPartElement<HTMLElement>("accordionHeader", (element) => {
                element.addEventListener("click", this.onHeaderClick.bind(this));
            });
            this._part_accordionHead = templatedElement.getPart<HTMLElement>("accordionHead").element;
            this._part_accordionSymbol = templatedElement.getPart<HTMLElement>("accordionSymbol").element;
            this._part_accordionContainer = templatedElement.getPart<HTMLElement>("accordionContainer").element;


            this.header = "Accordion";

            // TODO aggiornare le propriet di HeaderedControlBehaviour e ContentControlBehaviour
            this.onIsExpandedChanged();
        }

        private onHeaderClick(e: Event) {
            this.isExpanded = !this._isExpanded;
        }

        private onIsExpandedChanged() {
            var firstExpansion = false;
            if (this._isExpanded) {
                if (this._neverExpanded)
                    firstExpansion = true;
                this._neverExpanded = false;
                this._part_accordionContainer.style.display = "block";
                this._part_accordionSymbol.classList.add("expanded");
            } else {
                this._part_accordionContainer.style.display = "none";
                this._part_accordionSymbol.classList.remove("expanded");
            }
            this.events.trigger("onPropertyChanged", { propertyName: "isExpanded", propertyValue: this._isExpanded });
            this.events.trigger("onStateChanged", { isExpanded: this._isExpanded, isFirstExpansion: firstExpansion });
        }


        //// protected override
        //protected _onApplyTemplate() {
        //    this.initAccordionTemplate();
        //}


        public onStateChanged: EventSet<Accordion, AccordionStateChangedArgs>;

        public get isExpanded(): boolean {
            return this._isExpanded;
        }
        public set isExpanded(v: boolean) {
            if (this._isExpanded != v) {
                this._isExpanded = v;
                this.onIsExpandedChanged();
                this.events.trigger("onPropertyChanged", { propertyName: "isExpanded", propertyValue: v });
            }
        }
    }

    Builder.defineComponent({
        baseClass: HeaderedContentControl,
        classConstructor: Accordion,
        className: "Juice.Accordion",
        tagName: "jui-accordion"
    });
}