

namespace Juice {

    export class Dialog extends UIElement {

        protected static readonly DefaultDialogStyles = `
    .jui-dialog {
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        /*min-width: 240px;
        min-height: 100px;
        border-radius: 4px 4px 2px 2px;
        background-color: #fff;
        box-shadow: 0px 0px 5px 5px rgba(0, 0, 0, 0.07);
        border: 1px solid #c0c0c0;*/
        display: flex;
        flex-direction: column;
    }
    
    .jui-dialog-header {
        height: auto;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        /*padding: 5px 20px;
        font-weight: 600;
        border-radius: 4px 4px 0 0;
        border-bottom: 2px solid #fff;
        background-color: #f0f0f0;*/
    }
    
    .jui-dialog-content {
        height: auto;
        flex-grow: 1;
        /*border-radius: 0 0 2px 2px;
        padding: 10px 20px;*/
    }
    
    .jui-dialog-buttons {
        height: auto;
        /*text-align: center;
        padding-top: 10px;
        padding-bottom: 15px;*/
    }`;

        protected static readonly DefaultDialogHtmlTemplate = `
    <template template-class="Juice.Dialog">
        <div template-part="dialog" class="jui-dialog">
            <div template-part="header" class="jui-dialog-header"></div>
            <div template-part="content" class="jui-dialog-content"></div>
            <div template-part="buttons" class="jui-dialog-buttons"></div>
        </div>
    </template>`;

        private static readonly DefaultDialogTemplate = `<style>\n${Dialog.DefaultDialogStyles}\n</style>\n${Dialog.DefaultDialogHtmlTemplate}`;

        private _content: Content;

        private _part_dialog: HTMLElement;
        private _part_header: HTMLElement;
        private _part_content: HTMLElement;
        private _part_buttons: HTMLElement;

        constructor(title: string, template?: TemplateSource) {
            super(template || Dialog.DefaultDialogTemplate);

            this._part_header.innerText = title;
        }

        protected initializeTemplate(templatedElement: TemplatedElement) {
            super.initializeTemplate(templatedElement);

            this._part_dialog = templatedElement.getPart<HTMLElement>("dialog").element;
            this._part_header = templatedElement.getPart<HTMLElement>("header").element;
            this._part_content = templatedElement.getPart<HTMLElement>("content").element;
            this._part_buttons = templatedElement.getPart<HTMLElement>("buttons").element;
        }

        protected setContent(content: Content) {
            if (this._content !== content) {
                Internals.setContent(this._part_content, content);
            }
        }

        protected getButtonsContainer(): HTMLElement {
            return this._part_buttons;
        }

        public show(parent?: Element) {
            if (parent) {
                parent.appendChild(this.htmlElement);
            } else {
                let application = this.getApplication();
                if (application) {
                    application.rootElement.appendChild(this.htmlElement);
                } else {
                    document.body.appendChild(this.htmlElement);
                }
            }
        }

        public hide() {
            this.htmlElement.remove();
        }
    }

    Builder.defineComponent({
        baseClass: UIElement,
        classConstructor: Dialog,
        className: "Juice.Dialog"
    });

    export class ModalDialog extends Dialog {

        protected static readonly DefaultModalDialogStyles = `${Dialog.DefaultDialogStyles}\n

    .jui-dialog-mask {
        position: fixed;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        /*background-color: rgba(255, 255, 255, 0.5);*/
        z-index: 9000;
    }

    .jui-dialog.modal {
        position: absolute;
        z-index: 1;
    }
    
    /*.jui-dialog.modal .jui-dialog-buttons > .jui-button {
        margin: 0 2px;
    }*/`;

        protected static readonly DefaultModalDialogHtmlTemplate = `
    <template template-class="Juice.ModalDialog">
        <div template-part="dialogMask" class="jui-dialog-mask">
            <div template-part="dialog" class="jui-dialog">
                <div template-part="header" class="jui-dialog-header"></div>
                <div template-part="content" class="jui-dialog-content"></div>
                <div template-part="buttons" class="jui-dialog-buttons"></div>
            </div>
        </div>
    </template>`;

        protected _part_dialogMask: HTMLElement;

        private static readonly DefaultModalDialogTemplate = `<style>\n${ModalDialog.DefaultModalDialogStyles}\n</style>\n${ModalDialog.DefaultModalDialogHtmlTemplate}`;

        constructor(title: string, template?: TemplateSource) {
            super(title, template || ModalDialog.DefaultModalDialogTemplate);
        }

        protected initializeTemplate(templatedElement: TemplatedElement) {
            super.initializeTemplate(templatedElement);
            this._part_dialogMask = templatedElement.getPart<HTMLElement>("dialogMask").element;
            templatedElement.getPart<HTMLElement>("dialog").element.classList.add("modal");
        }
    }

    Builder.defineComponent({
        baseClass: Dialog,
        classConstructor: ModalDialog,
        className: "Juice.ModalDialog"
    });

    export class MessageDialog extends ModalDialog {

        public static showMessage(title: string, message: string, parent?: Element) {
            let dialog = new MessageDialog(title, message);
            dialog.show(parent);
        }

        constructor(title: string, message: string, template?: TemplateSource) {
            super(title, template);
            this.setContent(message);
        }

        protected initializeTemplate(templatedElement: TemplatedElement) {
            super.initializeTemplate(templatedElement);
            this._part_dialogMask = templatedElement.getPart<HTMLElement>("dialogMask").element;

            let buttonsContainer = this.getButtonsContainer();
            let okButton = new Button();
            okButton.content = "OK";
            okButton.onClick.add((s, e) => this.hide(), this);
            okButton.appendToHtmlElement(buttonsContainer);
            okButton.focus();
        }
    }

    Builder.defineComponent({
        baseClass: ModalDialog,
        classConstructor: MessageDialog,
        className: "Juice.MessageDialog"
    });

    export enum QuestionDialogButtons {
        OkCancel,
        YesNo
    }

    export class QuestionDialog extends ModalDialog {

        private _result?: boolean;
        private _onCompleted: (result: boolean) => void;

        constructor(title: string, message: string, buttons: QuestionDialogButtons, onCompleted: (result: boolean) => void) {
            super(title);

            this.setContent(message);

            this._onCompleted = onCompleted;

            let buttonsContainer = this.getButtonsContainer();

            let button1 = new Button();
            button1.content = buttons === QuestionDialogButtons.OkCancel ? "OK" : "Yes";
            button1.onClick.add((s, e) => this.closeDialog(true), this);
            button1.appendToHtmlElement(buttonsContainer);

            let button2 = new Button();
            button2.content = buttons === QuestionDialogButtons.OkCancel ? "Cancel" : "No";
            button2.onClick.add((s, e) => this.closeDialog(false), this);
            button2.appendToHtmlElement(buttonsContainer);

            button1.focus();
        }

        private closeDialog(result: boolean) {
            this._result = result;
            this.hide();
            if (typeof this._onCompleted === "function")
                this._onCompleted(this._result);
        }

        public get result(): boolean {
            return this._result;
        }
    }

    Builder.defineComponent({
        baseClass: ModalDialog,
        classConstructor: QuestionDialog,
        className: "Juice.QuestionDialog"
    });
}