
namespace Juice {

    let DefaultHtmlContainerTemplate = `
<template template-class="HtmlContainer">
    <div template-part="container"></div>
</template>`;

    export class HtmlContainer extends UIElement {

        private _part_container: HTMLElement;

        constructor(template?: Template) {
            super(template || DefaultHtmlContainerTemplate);
        }

        protected initializeTemplate(templatedElement: TemplatedElement) {
            super.initializeTemplate(templatedElement);
            this._part_container = templatedElement.getPart<HTMLElement>("container").element;
        }

        public get html(): string {
            return this._part_container.innerHTML;
        }

        public set html(v: string) {
            this._part_container.innerHTML = v;
        }

        public addContent(...content: HTMLElement[]) {
            for (let element of content) {
                this._part_container.appendChild(element);
            }
        }

        public setContent(...content: HTMLElement[]) {
            this.html = "";
            this.addContent(...content);
        }
    }

    Builder.defineComponent({
        baseClass: UIElement,
        classConstructor: HtmlContainer,
        className: "Juice.HtmlContainer",
        tagName: "jui-html-container"
    });
}