/// <reference path="Builder.ts" />
/// <reference path="UIElement.ts" />
/// <reference path="UIElementBehaviour.ts" />

namespace Juice {

    export class ItemsControlBehaviour extends UIElementBehaviour {

        private _items: Collection<UIElement>;

        private initializeItemsControlBehaviour(): void {
            this._items = new Collection<UIElement>();
            this._items.onItemsAdded.add(this.onItemsCollectionChanged, this);
            this._items.onItemsRemoved.add(this.onItemsCollectionChanged, this);
            this._items.onCollectionCleared.add(this.onItemsCollectionChanged, this);
        }

        private onItemsCollectionChanged(s: Collection<UIElement>, e: CollectionChangedEventArgs<UIElement>) {
            this._onItemsCollectionChanged(e);
        }

        // protected virtual
        protected _onItemsCollectionChanged(args: CollectionChangedEventArgs<UIElement>): void { }

        public get items() {
            return this._items;
        }
    }

    export class ItemsControl extends UIElement {
        constructor(template?: TemplateSource) {
            super(template);
            UIElementBehaviour.initialize(ItemsControlBehaviour, this);
        }
    }

    export interface ItemsControl extends ItemsControlBehaviour {
        new(template?: Template): ItemsControl;
    }

    Builder.applyBehaviours(ItemsControl, [ItemsControlBehaviour]);

    Builder.defineComponent({
        baseClass: UIElement,
        classConstructor: ItemsControl,
        className: "Juice.ItemsControl",
        tagName: "jui-items-control"
    });
}