/// <reference path="uielement.ts" />
/// <reference path="uielementbehaviour.ts" />
/// <reference path="Control.ts" />


namespace Juice {

    export type ValueContent = undefined | string | number | boolean | object;
    export type VisualContent = HTMLElement | UIElement | NodeList;
    export type ContentArray = Array<ValueContent> | Array<VisualContent>
    export type Content = string | number | boolean | object | HTMLElement | UIElement | ContentArray;

    export class ContentControlBehaviour extends UIElementBehaviour {

        private _contentValue: Content = null;
        private _contentElement: HTMLElement = null;
        private _contentElementSelector: Selector = null;

        //protected constructor() {
        //    super();
        //}

        private initializeContentControlBehaviour(): void {
            (<any>this).registerApplyTemplateListener(() => {
                this.onContentPropertyChanged();
            });
        }

        private onContentPropertyChanged() {
            var contentElement = this.getContentElement();
            if (contentElement) {
                Internals.setContent(contentElement, this._contentValue);
            }
            this._onContentChanged(contentElement, this._contentValue);
        }

        private getContentElement() {
            if (this._contentElementSelector) {
                this._contentElement = this._contentElementSelector.query() as HTMLElement;
            }
            //if (!this._contentElement) {
            //    if (this.template) {
            //        this._contentElement = this.template.htmlElement.querySelector("*[template-part='content']");
            //    }
            //}
            return this._contentElement;
        }

        // protected virtual
        protected _onContentChanged(contentElement: HTMLElement, content: Content): void { }

        protected _setContentElement(contentElement: Selector | HTMLElement): void {
            if (contentElement instanceof HTMLElement) {
                this._contentElement = contentElement;
                this._contentElementSelector = null;
            } else {
                this._contentElementSelector = contentElement;
            }
            this.onContentPropertyChanged();
        }

        public get content(): Content {
            return this._contentValue;
        }
        public set content(v: Content) {
            if (this._contentValue != v) {
                this._contentValue = v;
                this.onContentPropertyChanged();
                this.events.trigger("onPropertyChanged", { propertyName: "content", propertyValue: v });
            }
        }
    }

    export class ContentControl extends Control {

        private static readonly DefaultContentControlTemplate = `
<template template-class="Juice.ContentControl">
    <div template-part="frame" class="content-control-frame">
        <div template-part="content" class="content-control-content"></div>
    </div>
</template>`;

        constructor(template?: TemplateSource) {
            super(template || ContentControl.DefaultContentControlTemplate);
        }

        protected initializeTemplate(templatedElement: TemplatedElement) {
            super.initializeTemplate(templatedElement);
            UIElementBehaviour.initialize(ContentControlBehaviour, this);
            this._setContentElement(templatedElement.getPart<HTMLElement>("content").element);
        }

        protected _onApplyTemplate(templatedElement: TemplatedElement) {
            super._onApplyTemplate(templatedElement);
            this.initializeTemplate(templatedElement);
        }
    }

    export interface ContentControl extends ContentControlBehaviour {
        new(template?: string | HTMLTemplateElement | HTMLElement): ContentControl;
    }

    Builder.applyBehaviours(ContentControl, [ContentControlBehaviour]);

    Builder.defineComponent({
        baseClass: UIElement,
        classConstructor: ContentControl,
        className: "Juice.ContentControl",
        tagName: "jui-content-control"
    });
}