/// <reference path="UIElement.ts" />
/// <reference path="HeaderedControl.ts" />
/// <reference path="ContentControl.ts" />

namespace Juice {

    export class HeaderedContentControl extends UIElement {
        constructor(template?: TemplateSource) {
            super(template);
        }

        protected initializeTemplate(templatedElement: TemplatedElement) {
            super.initializeTemplate(templatedElement);
            UIElementBehaviour.initialize(HeaderedControlBehaviour, this);
            UIElementBehaviour.initialize(ContentControlBehaviour, this);
            this._setHeaderElement(templatedElement.getPart<HTMLElement>("header").element);
            this._setContentElement(templatedElement.getPart<HTMLElement>("content").element);
        }
    }

    export interface HeaderedContentControl extends HeaderedControlBehaviour, ContentControlBehaviour {
        new(template?: string | HTMLTemplateElement | HTMLElement): HeaderedContentControl;
    }

    Builder.applyBehaviours(HeaderedContentControl, [HeaderedControlBehaviour, ContentControlBehaviour]);

    Builder.defineComponent({
        baseClass: UIElement,
        classConstructor: HeaderedContentControl,
        className: "Juice.HeaderedContentControl",
        tagName: "jui-headered-content-control"
    });
}