
namespace Juice {

    export class Control extends UIElement {

        constructor(template?: TemplateSource) {
            super(template);
        }

        protected initializeTemplate(templatedElement: TemplatedElement) {
            super.initializeTemplate(templatedElement);
        }

        public get isEnabled() {
            return !this.htmlElement.hasAttribute("disabled");
        }
        public set isEnabled(v: boolean) {
            if (!!v) {
                this.htmlElement.removeAttribute("disabled");
            } else {
                this.htmlElement.setAttribute("disabled", "disabled");
            }
        }
    }

    Builder.defineComponent({
        baseClass: UIElement,
        classConstructor: Control,
        className: "Juice.Control",
        tagName: "jui-control"
    });
}