
namespace Juice {

    let _applications = new Map<string, Application>();

    export function getApplication(appId: string) {
        if(_applications.has(appId)) {
            return _applications.get(appId);
        }
        return null;
    }

    export function registerApplication(application: Application) {
        if(!_applications.has(application.appId)) {
            _applications.set(application.appId, application);
        }
    }

    export function unregisterApplication(application: Application) {
        if(!_applications.has(application.appId)) {
            _applications.delete(application.appId);
            return true;
        }
        return false;
    }
}