
namespace Juice {

    export class Selector {

        constructor(
            public selector: string,
            public context?: ParentNode | (() => ParentNode)
        ) { }

        private getCurrentContext(): ParentNode {
            if (typeof this.context === "function") {
                return this.context();
            } else {
                return this.context;
            }
        }

        public query() {
            if (this.context) {
                return this.getCurrentContext().querySelector(this.selector);
            }
            return document.querySelector(this.selector);
        }

        public queryAll() {
            if (this.context) {
                return this.getCurrentContext().querySelectorAll(this.selector);
            }
            return document.querySelectorAll(this.selector);
        }
    }

    Builder.defineClass({
        baseClass: Object,
        classConstructor: Selector,
        className: "Juice.Selector"
    });
}