
/// <reference path="HeaderedItemsControl.ts" />

namespace Juice {

    export class Toolbar extends HeaderedItemsControl {

        protected static readonly DefaultToolbarStyles = `
    .jui-toolbar {
        display: flex;
        flex-flow: row;
        align-items: center;
    }

    .jui-toolbar-label {
        width: auto;
        /*padding: 0 10px;*/
        white-space: nowrap;
    }

    .jui-toolbar-items {
        width: 100%;
        white-space: nowrap;
        overflow-x: hidden;
        align-items: center;
        display: flex;
    }

    .jui-toolbar-items > * {
        display: inline-block;
        vertical-align: middle;
    }

    .jui-toolbar-button {
        display: inline-block;
        height: 100%;
        /*min-width: 32px;
        min-height: 32px;
        color: #222;
        background-color: transparent;
        outline: 0;
        border: 0;
        border-radius: 0;
        padding: 4px 4px;*/
        background-position: center center;
        background-repeat: no-repeat;
        background-size: auto;
        position: relative;
        cursor: default;
        user-select: none;
    }

    .jui-toolbar-menu-button > svg {
        display: block;
        /*position: relative;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);*/
    }`;

        protected static readonly DefaultToolbarHtmlTemplate = `
<template template-class="Juice.Toolbar">
    <div class="jui-toolbar">
        <div template-part="header" class="jui-toolbar-label"></div>
        <div template-part="toolbarItems" class="jui-toolbar-items"></div>
        <button disabled template-part="toolbarMenuButton" class="jui-toolbar-button jui-toolbar-menu-button">
            <svg class="jui-toolbar-icon-svg" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                <circle cx="12" cy="5.5" r="1.5" />
                <circle cx="12" cy="12" r="1.5" />
                <circle cx="12" cy="18.5" r="1.5" />
            </svg>
        </button>
    </div>
</template>`;

        private static readonly DefaultToolbarTemplate = `<style>\n${Toolbar.DefaultToolbarStyles}\n</style>\n${Toolbar.DefaultToolbarHtmlTemplate}`;

        private _part_toolbarItems: HTMLElement;

        constructor(template?: TemplateSource) {
            super(template || Toolbar.DefaultToolbarTemplate);
        }

        protected initializeTemplate(templatedElement: TemplatedElement) {
            super.initializeTemplate(templatedElement);
            this._part_toolbarItems = templatedElement.getPart<HTMLElement>("toolbarItems").element;
        }

        private onItemsAdded(e: CollectionChangedEventArgs<UIElement>): void {
            var refElement = this._part_toolbarItems.children[e.index];
            if (!refElement) {
                for (let i = 0; i < e.items.length; i++) {
                    let item = e.items[i];
                    if (item instanceof Button) {
                        item.htmlElement.classList.remove("jui-button");
                        item.htmlElement.classList.add("jui-toolbar-button");
                    }
                    this._part_toolbarItems.appendChild(e.items[i].htmlElement);
                }
            } else {
                for (let i = 0; i < e.items.length; i++) {
                    var child = e.items[i].htmlElement;
                    this._part_toolbarItems.insertBefore(child, refElement);
                    refElement = child;
                }
            }
        }

        private onItemsRemoved(e: CollectionChangedEventArgs<UIElement>) {
            // TODO
            console.warn("Toolbar.onItemsRemoved not implemented");
        }

        private onCollectionCleared(e: CollectionChangedEventArgs<UIElement>) {
            while (this._part_toolbarItems.firstChild)
                this._part_toolbarItems.removeChild(this._part_toolbarItems.firstChild);
        }


        protected _onItemsCollectionChanged(args: CollectionChangedEventArgs<UIElement>): void {
            switch (args.action) {
                case CollectionChangedAction.Added:
                    this.onItemsAdded(args);
                    break;
                case CollectionChangedAction.Removed:
                    this.onItemsRemoved(args);
                    break;
                case CollectionChangedAction.Cleared:
                    this.onCollectionCleared(args);
                    break;
            }
        }

        protected _onApplyTemplate(templatedElement: TemplatedElement) {
            super._onApplyTemplate(templatedElement);
            this.initializeTemplate(templatedElement);
        }

        public addButton(content?: Content): Button {
            let btn = new Button();
            if (typeof content !== "undefined") {
                btn.content = content;
            }
            this.items.add(btn);
            return btn;
        }

        public addSeparator(): HtmlContainer {
            let sep = new HtmlContainer();
            sep.htmlElement.classList.add("jui-toolbar-item-separator");
            this.items.add(sep);
            return sep;
        }

        public addLabel(text: string): HtmlContainer {
            let label = new HtmlContainer();
            label.htmlElement.textContent = text;
            this.items.add(label);
            return label;
        }
    }

    Builder.defineComponent({
        baseClass: ItemsControl,
        classConstructor: Toolbar,
        className: "Juice.Toolbar",
        tagName: "jui-toolbar"
    });
}