
namespace Juice {

    export type TemplateSource = Template | string | HTMLElement;


    export class Template {

        private static loadTemplateDocument(templateDocument: Document): Template {
            let styleNode = templateDocument.querySelector("style");
            let templateNode = templateDocument.querySelector("template");
            let scriptNode = templateDocument.querySelector("script");

            let className = "";
            if (templateNode.hasAttribute("template-class")) {
                className = templateNode.getAttribute("template-class");
            }
            return new Template(className, templateNode, styleNode, scriptNode);
        }

        public static from(html: string | Document): Template {
            let doc: Document;
            if (typeof html === "string") {
                var parser = new DOMParser();
                doc = parser.parseFromString(html, 'text/html');
            } else {
                doc = html;
            }
            return Template.loadTemplateDocument(doc);
        }


        private _element: HTMLTemplateElement;
        private _styles: HTMLStyleElement;
        private _script: HTMLScriptElement;

        constructor(className: string, element: HTMLTemplateElement, styles: HTMLStyleElement, script: HTMLScriptElement) {
            this.templateClass = className;
            this._element = element;
            this._styles = styles;
            this._script = script;
        }

        public readonly templateClass: string;

        public importTemplate(): TemplatedElement {

            if (this._element.content.children.length > 1) {
                let rootElement = document.createElement("div");
                let children = Array.from(this._element.content.childNodes);
                for (let element of children) {
                    rootElement.appendChild(element);
                }
                this._element.content.append(rootElement);
            }

            if (this._element.content.children.length == 0) {
                throw new Error("missing template content");
            }

            let importedElement = document.importNode(this._element.content, true);

            return new TemplatedElement(this, importedElement);
        }

        public importStyles(): HTMLStyleElement {

            if (this._styles) {
                return document.importNode(this._styles, true);
            }

            // TODO
            return null;
        }
    }

    Builder.defineClass({
        baseClass: Object,
        classConstructor: Template,
        className: "Juice.Template"
    });

    export class TemplatedElement {

        private _template: Template;
        private _parts: Map<string, Element>;
        private _templateNode: ParentNode;

        constructor(template: Template, templateNode: ParentNode) {
            this._template = template;
            this._templateNode = templateNode;
            this._parts = new Map<string, Element>();
            this.findParts();
        }


        private findParts() {
            let allParts = this._templateNode.querySelectorAll('*[template-part]');
            for (let part of allParts) {
                let partName = part.getAttribute("template-part");
                this._parts.set(partName, part);
            }
        }


        public get rootElement(): Element {
            return this._templateNode.children.item(0);
        }


        public get templateDefinition() {
            return this._template;
        }


        public getPart<T>(name: string, throwIfNotExists?: boolean): TemplatePart<T> {
            if (this._parts.has(name)) {
                let part = this._parts.get(name);
                let element = part as any as T;
                return new TemplatePart<T>(name, element);
            } else {
                if (throwIfNotExists) {
                    throw new Error(`part whit name ${name} not exists`);
                }
                return null;
            }
        }

        public withPartElement<T>(name: string, action: (element: T) => void, throwIfNotExists?: boolean): T {
            if (this._parts.has(name)) {
                let part = this._parts.get(name);
                let element = part as any as T;
                action(element);
                return element;
            } else {
                if (throwIfNotExists) {
                    throw new Error(`part whit name ${name} not exists`);
                } else {
                    console.warn(`part whit name ${name} not exists`);
                }
                return null;
            }
        }
    }

    Builder.defineClass({
        baseClass: Object,
        classConstructor: TemplatedElement,
        className: "Juice.TemplatedElement"
    });

    export class TemplatePart<T> {

        constructor(name: string, element: T) {
            this.name = name;
            this.element = element;
        }

        public readonly name: string;

        public readonly element: T;
    }

    Builder.defineClass({
        baseClass: Object,
        classConstructor: TemplatePart,
        className: "Juice.TemplatePart"
    });

    export class TemplateManager {

        private static _templateDefinitions: Template[] = [];
        private static _importedClassesStyles: Set<string> = new Set<string>();

        public static createTemplate(templateSource: TemplateSource): Template {

            if (templateSource instanceof Template) {
                return templateSource;
            }

            let result: Template = null;

            if (typeof templateSource === "string") {
                if (templateSource.charAt(0) === "@") {
                    throw new Error("not implemented");
                    // result = TemplateManager.findByName(templateSource.substr(1));
                    // if (!result) {
                    //     throw new Error("unresolved template resource '" + templateSource + "'");
                    // }
                } else {
                    // assume si tratti di una stinga contenente codice html
                    result = Template.from(templateSource);
                    if (!result) {
                        throw new Error("unresolved template resource, invalid html code");
                    }
                }
            }

            this.addTemplate(result);
            return result;
        }

        public static addTemplate(template: Template) {
            if (!this._importedClassesStyles.has(template.templateClass)) {
                let classStyles = template.importStyles();
                if (classStyles) {
                    document.head.appendChild(classStyles);
                    this._importedClassesStyles.add(template.templateClass);
                }
            }
            this._templateDefinitions.push(template);
        }

        public static getTemplate(className: string): Promise<Template> {
            let templateDefs = this._templateDefinitions.filter((v, i, a) => v.templateClass === className);
            if (templateDefs.length > 0) {
                return new Promise<Template>(resolve => resolve(templateDefs[0]));
            } else {
                return new Promise<Template>((resolve, reject) => {
                    this
                        .loadExternal(className)
                        .then(result => {
                            this.addTemplate(result)
                            resolve(result);
                        })
                        .catch(err => {
                            reject(err);
                        });

                });
            }
        }

        public static loadExternal(className: string): Promise<Template> {
            let templatePath = className.split(".").join("/") + ".html";
            return new Promise<Template>((resolve, reject) => {
                Internals.getTemplateFile(templatePath)
                    .then(result => {
                        let template = Template.from(result);
                        if (template.templateClass !== className) {
                            throw new Error("invalid template class name");
                        }
                        resolve(template);
                    })
                    .catch(err => {
                        reject(err);
                    });
            });
        }
    }

    Builder.defineClass({
        baseClass: Object,
        classConstructor: TemplateManager,
        className: "Juice.TemplateManager"
    });
}