
/// <reference path="Template.ts" />
/// <reference path="ContentControl.ts" />

namespace Juice {

    class ApplicationFrame extends ContentControl {

        protected static readonly DefaultApplicationFrameStyles = `
    .jui-app {
        height: 100%;
    }

    .jui-app-content {
        height: 100%;
    }`;

        protected static readonly DefaultApplicationFrameHtmlTemplate = `
    <template template-class="Juice.Application">
        <div class="jui-app" template-part="appRoot">
            <div class="jui-app-content" template-part="content"></div>
        </div>
    </template>`;

        private static readonly DefaultApplicationFrameTemplate = `<style>\n${ApplicationFrame.DefaultApplicationFrameStyles}\n</style>\n${ApplicationFrame.DefaultApplicationFrameHtmlTemplate}`;

        private _applicationService: ApplicationService;
        private _part_appRoot: HTMLElement;

        constructor(applicationService: ApplicationService, template?: TemplateSource) {
            super(template || ApplicationFrame.DefaultApplicationFrameTemplate);
            this._applicationService = applicationService;
        }

        protected initializeTemplate(templatedElement: TemplatedElement) {
            super.initializeTemplate(templatedElement);
            this._part_appRoot = templatedElement.getPart<HTMLElement>("appRoot").element;
        }

        public get application(): Application { return this._applicationService.application; }
    }

    export interface ApplicationOptions {
        [name: string]: () => any,
        rootNode?: () => Node,
        mainElement?: () => UIElement,
        appId?: () => string,
        currentTheme?: () => string
    }

    class ApplicationOptionsReader {

        private _options: ApplicationOptions;

        constructor(options: ApplicationOptions) {
            this._options = options;
        }

        public rootNode() {
            return this.getValue("rootNode", null);
        }

        public mainElement() {
            return this.getValue("mainElement", null);
        }

        public appId() {
            return this.getValue("appId", null);
        }

        public currentTheme() {
            return this.getValue("currentTheme", null);
        }

        public getValue(name: string, defaultValue: any = null) {
            if (typeof this._options[name] === "function") {
                return this._options[name]();
            }
            return defaultValue;
        }
    }

    export class ApplicationService {

        constructor(
            public readonly application: Application,
            //setAppIdHandler: (appId: string) => void,
            setCurrentPageHandler: (page: Page) => void,
            setCurrentThemeHandler: (theme: string) => void
        ) {
            //this.setAppId = setAppIdHandler.bind(application);
            this.setCurrentPage = setCurrentPageHandler.bind(application);
            this.setCurrentTheme = setCurrentThemeHandler.bind(application);
        }

        //public readonly setAppId: (appId: string) => void;
        public readonly setCurrentPage: (page: Page) => void;
        public readonly setCurrentTheme: (theme: string) => void;
    }

    export class Application {

        private readonly _defaultOptions: ApplicationOptions;
        private _isRunning = false;
        private _root: Node;
        private _configure: () => ApplicationOptions;
        private _options: ApplicationOptions;
        private _appFrame: ApplicationFrame;
        private _appId: string;

        private _currentPage: Page;
        private _currentTheme: string;

        protected readonly applicationService: ApplicationService;
        protected readonly events: EventsManager;

        constructor() {
            this.events = new EventsManager(this);
            this.onApplicationReady = new EventSet<Application, EventArgs>(this.events, "onApplicationReady", this);
            this.onThemeChanged = new EventSet<Application, EventArgs>(this.events, "onThemeChanged", this);
            this._defaultOptions = {
                mainElement: null,
                rootNode: null
            };
            this.applicationService = new ApplicationService(
                this,
                this.setCurrentPage,
                this.setCurrentTheme
            );
        }


        protected initialize() {

            var optionsReader = new ApplicationOptionsReader(this._options);

            this._appId = optionsReader.appId() || Internals.getFreeUid();
            this._root = optionsReader.rootNode();

            this._appFrame = new ApplicationFrame(this.applicationService);
            this._appFrame.htmlElement.setAttribute("app-id", this._appId);
            this._appFrame.content = optionsReader.mainElement();

            this.setCurrentTheme(optionsReader.currentTheme());

            this._root.appendChild(this._appFrame.htmlElement);

            Juice.registerApplication(this);
        }

        private setCurrentPage(page: Page) {
            if (this._currentPage) {
                this.currentPage.htmlElement.remove();
            }
            this._currentPage = page;
            this._root.appendChild(this._currentPage.htmlElement);
        }

        private setCurrentTheme(theme: string) {
            if (this._currentTheme !== theme) {
                // TODO
                if (this._currentTheme) {
                    this.rootElement.classList.remove(this._currentTheme);
                }
                this._currentTheme = theme;
                if (this._currentTheme) {
                    this.rootElement.classList.add(this._currentTheme);
                }
                this.onThemeChanged.trigger();
            }
        }

        private readOptions(options: ApplicationOptions) {
            this._options = {
                mainElement: this.getOptionsValueOrDefault(options, "mainElement", this._defaultOptions.mainElement),
                rootNode: this.getOptionsValueOrDefault(options, "rootNode", this._defaultOptions.rootNode),
                currentTheme: this.getOptionsValueOrDefault(options, "currentTheme", this._defaultOptions.currentTheme)
            };
        }

        private getOptionsValueOrDefault<T>(options: ApplicationOptions, name: string, defaultValue: () => T): () => T {
            if (name in options) {
                return options[name] as () => T;
            }
            return defaultValue;
        }

        private assertIsRunning() {
            if (!this._isRunning) {
                throw new Error("application not running");
            }
        }



        public readonly onApplicationReady: EventSet<Application, EventArgs>;

        public readonly onThemeChanged: EventSet<Application, EventArgs>;


        public configure(configure: () => ApplicationOptions) {
            if (this._isRunning) {
                throw new Error("application is running");
            }
            if (typeof configure !== "function") {
                throw new Error("invalid configure function");
            }
            this._configure = configure;
        }

        // public showPage(page: Page) {
        //     this.assertIsRunning();
        //     if(page instanceof Page) {
        //         this.setCurrentPage(page);
        //     }
        // }

        public run() {
            if (this._isRunning) {
                return;
            }
            this._isRunning = true;
            if (this._configure) {
                let options = this._configure();
                this.readOptions(options);
            }
            window.addEventListener("DOMContentLoaded", () => {
                this.initialize();
                this.onApplicationReady.trigger();
            });
        }


        public get appId() { return this._appId; }

        public get rootElement() { return this._appFrame.htmlElement; }

        public get currentPage(): Page { return this._currentPage; }

        public get currentTheme(): string { return this._currentTheme; }

        // public setTheme(theme: string) {
        //     // TODO
        //     if(this._currentTheme) {
        //         this.rootElement.classList.remove(this._currentTheme);
        //     }
        //     this._currentTheme = theme;
        //     if(this._currentTheme) {
        //         this.rootElement.classList.add(this._currentTheme);
        //     }
        // }
    }

    Builder.defineClass({
        baseClass: Object,
        classConstructor: Application,
        className: "Juice.Application"
    });
}