
namespace Juice {

    export enum CollectionChangedAction {
        Added,
        Removed,
        Cleared
    }

    export class CollectionChangedEventArgs<T> implements EventArgs {
        constructor(
            public readonly action: CollectionChangedAction,
            public readonly index: number,
            items: T[]
        ) { 
            this.items = items || []
        }

        public readonly items: T[]
    }

    export class Collection<T> {

        private _items: T[];
        private _events: EventsManager;


        constructor(initialItems?: T[]) {

            if (initialItems) {
                this._items = initialItems;
            }
            else {
                this._items = [];
            }

            this._events = new EventsManager(this);

            this.onItemsAdded = new EventSet<Collection<T>, CollectionChangedEventArgs<T>>(this._events, "onItemsAdded");
            this.onItemsRemoved = new EventSet<Collection<T>, CollectionChangedEventArgs<T>>(this._events, "onItemsRemoved");
            this.onCollectionCleared = new EventSet<Collection<T>, CollectionChangedEventArgs<T>>(this._events, "onCollectionCleared");
        }

        private triggerCollectionChanged(action: CollectionChangedAction, index: number, items: T[]) {
            var args = new CollectionChangedEventArgs<T>(action, index, items);
            switch (action) {
                case CollectionChangedAction.Added: {
                    this._events.trigger("onItemsAdded", args);
                    break;
                }
                case CollectionChangedAction.Removed: {
                    this._events.trigger("onItemsRemoved", args);
                    break;
                }
                case CollectionChangedAction.Cleared: {
                    this._events.trigger("onCollectionCleared", args);
                    break;
                }
            }
        }

        public insert(index: number, item: T): number {
            if (index < 0)
                index = 0;
            if (index >= this._items.length)
                index = this._items.length;
            if (index === this._items.length) {
                this._items.push(item);
            }
            else {
                this._items.splice(index, 0, item);
            }
            index = this._items.length - 1;
            this.triggerCollectionChanged(CollectionChangedAction.Added, index, [item]);
            return index;
        }

        public insertRange(index: number, items: T[]): number {
            var i;
            if (index < 0)
                index = 0;
            if (index >= this._items.length)
                index = this._items.length;
            if (index === this._items.length) {
                for (i = 0; i < items.length; i++) {
                    this._items.push(items[i]);
                }
            }
            else {
                this._items.splice(index, 0, ...items);
            }
            if (this._items.length > index) {
                this.triggerCollectionChanged(CollectionChangedAction.Added, index, items);
            }
            return index;
        }


        public get length() {
            return this._items.length;
        }


        public forEach(callback: (item: T, index: number, collection: Collection<T>) => void): void {
            for (var i = 0; i < this._items.length; i++) {
                callback(this._items[i], i, this);
            }
        }

        public mapToArray(callback: (item: T, index: number, collection: Collection<T>) => T): T[] {
            var result = []
            for (var i = 0; i < this._items.length; i++) {
                let resultItem = callback(this._items[i], i, this);
                result.push(resultItem);
            }
            return result;
        }

        public add(item: T) {
            return this.insert(this._items.length, item);
        }

        public addRange(items: T[]) {
            return this.insertRange(this._items.length, items);
        }

        public at(index: number) {
            return this._items[index];
        }

        public clear() {
            var items = this._items;
            this._items = [];
            this.triggerCollectionChanged(CollectionChangedAction.Cleared, 0, items);
        }

        public contains(item: any, selector: (item: T) => any) {
            if (typeof selector === "function") {
                for (var i = 0; i < this._items.length; i++) {
                    if (selector(this._items[i]) === item) {
                        return true;
                    }
                }
                return false;
            }
            else {
                return this._items.indexOf(item) >= 0;
            }
        }

        public indexOf(item: any, selector?: (item: T) => any) {
            if (typeof selector === "function") {
                var result = -1;
                for (var i = 0; i < this._items.length; i++) {
                    if (selector(this._items[i]) === item) {
                        result = i;
                        break;
                    }
                }
                return result;
            }
            else {
                return this._items.indexOf(item);
            }
        }

        public remove(item: any, selector?: (item: T) => any) {
            var itemIndex = this.indexOf(item, selector);
            if (itemIndex === -1)
                return false;
            this._items.splice(itemIndex, 1);
            this.triggerCollectionChanged(CollectionChangedAction.Removed, itemIndex, [item]);
            return true;
        }

        public removeAt(index: number) {
            if (index < 0 || index >= this._items.length)
                return null;
            var item = this._items[index];
            this._items.splice(index, 1);
            this.triggerCollectionChanged(CollectionChangedAction.Removed, index, [item]);
            return item;
        }

        public removeItems(items: T[]) {
            var itemIndex = -1;
            var removedItems: Array<T> = [];
            for (var i = 0; i < items.length; i++) {
                itemIndex = this.indexOf(items[i]);
                if (itemIndex === -1)
                    continue;
                var removed = this._items.splice(itemIndex, 1);
                removedItems.push(...removed);
            }
            this.triggerCollectionChanged(CollectionChangedAction.Removed, itemIndex, removedItems);
            return removedItems;
        }

        public removeRange(index: number, length: number) {
            if (typeof index !== "number" || index < 0)
                index = 0;
            if (!length) {
                length = this._items.length - index;
            }
            var removedItems = this._items.splice(index, length);
            this.triggerCollectionChanged(CollectionChangedAction.Removed, index, removedItems);
            return removedItems;
        }

        public removeAll(predicate?: (item: T, index?: number) => boolean) {
            let index: number = null;
            let removedItems: Array<T> = [];
            if (typeof predicate === "function") {
                let newItems: Array<T> = [];
                this._items.forEach((v, i, a) => {
                    if (predicate(v, i)) {
                        if (index == null)
                            index = i;
                        removedItems.push(v);
                    } else {
                        newItems.push(v);
                    }
                });
                this._items = newItems;
            }
            else {
                index = 0;
                removedItems = this._items.splice(index, this._items.length);
            }

            if (removedItems.length > 0)
                this.triggerCollectionChanged(CollectionChangedAction.Removed, index, removedItems);

            return removedItems;
        }

        public toArray() {
            return this._items.slice(0);
        }


        public readonly onItemsAdded: EventSet<Collection<T>, CollectionChangedEventArgs<T>>;

        public readonly onItemsRemoved: EventSet<Collection<T>, CollectionChangedEventArgs<T>>;

        public readonly onCollectionCleared: EventSet<Collection<T>, CollectionChangedEventArgs<T>>;
    }

    Builder.defineClass({
        baseClass: Object,
        classConstructor: Collection,
        className: "Juice.Collection"
    });
}