/// <reference path="Builder.ts" />
/// <reference path="BindableObject.ts" />

namespace Juice {

    export class UIElement extends BindableObject {

        private _template: TemplateSource;
        private _templatedElement: TemplatedElement;
        private _htmlElement: Element;
        private _data: any;
        private _applyTemplateListeners: Array<(templatedElement: TemplatedElement) => void> = [];

        constructor(template?: TemplateSource) {
            super();

            this._htmlElement = document.createElement("div");
            this.uid = Internals.getFreeUid();
            this.resources = new Map<string, any>();
            this.init(template);
        }

        private init(template?: TemplateSource) {
            if (template) {
                this._template = TemplateManager.createTemplate(template);
                this._templatedElement = this._template.importTemplate();
                this.initializeTemplate(this._templatedElement);
            }
            else {
                TemplateManager
                    .getTemplate((<any>this).constructor.className)
                    .then(result => {
                        this.template = result;
                    })
                    .catch(err => { });
            }
        }

        protected readonly resources: Map<string, any>;

        protected initializeTemplate(templatedElement: TemplatedElement) {

            let templateHtmlElement = templatedElement.rootElement;
            templateHtmlElement.setAttribute("uid", this.uid.toString());

            if (this._htmlElement) {
                if (this._htmlElement.parentElement) {
                    this._htmlElement.parentElement.insertBefore(templateHtmlElement, this._htmlElement);
                    this._htmlElement.parentElement.removeChild(this._htmlElement);
                }
            }

            if (this._htmlElement.parentElement) {
                this._htmlElement.parentElement.insertBefore(templateHtmlElement, this._htmlElement);
                this._htmlElement.remove();
            }
            this._htmlElement = templateHtmlElement;

            Internals.registerElement(this.uid, this, this._htmlElement);
        }


        // protected virtual
        protected _onApplyTemplate(templatedElement: TemplatedElement) { }

        protected registerApplyTemplateListener(handler: (templatedElement: TemplatedElement) => void) {
            handler = handler.bind(this);
            this._applyTemplateListeners.push(handler);
        }


        public get data(): any {
            return this._data;
        }
        public set data(v: any) {
            this._data = v;
            this.onPropertyChanged.trigger({ propertyName: "data", propertyValue: v });
        }

        public readonly uid: string;

        public get template(): TemplateSource {
            return this._template;
        }
        public set template(v: TemplateSource) {
            this.init(v);
            this._onApplyTemplate(this._templatedElement);
            for (let handler of this._applyTemplateListeners)
                handler(this._templatedElement);
            this.onPropertyChanged.trigger({ propertyName: "template", propertyValue: v });
        }

        public get htmlElement(): Element {
            return this._htmlElement;
        }


        public appendToHtmlElement(parentElement: HTMLElement | string) {

            if (parentElement == null) {
                parentElement = document.body;
            }
            else if (typeof parentElement === "string") {
                parentElement = <HTMLElement>document.querySelector(parentElement);
            }

            if (parentElement instanceof HTMLElement) {
                parentElement.appendChild(this._htmlElement);
                return true;
            }

            return false;
        }

        public getApplicationId() {
            if (this._htmlElement.parentElement) {
                let element = this._htmlElement;
                while (element != null) {
                    if (element.hasAttribute("app-id")) {
                        return element.getAttribute("app-id");
                    }
                    element = element.parentElement;
                }
            }
            return null;
        }

        public getApplication() {
            let appId = this.getApplicationId();
            if (appId) {
                return Juice.getApplication(appId);
            }
            return null;
        }
    }

    Builder.defineComponent({
        baseClass: BindableObject,
        className: "Juice.UIElement",
        classConstructor: UIElement
    });
}