/// <reference path="UIElement.ts" />
/// <reference path="UIElementBehaviour.ts" />

namespace Juice {

    export class HeaderedControlBehaviour extends UIElementBehaviour {

        private _headerValue: Content = null;
        private _headerElement: HTMLElement = null;
        private _headerElementSelector: Selector = null;

        //protected constructor() {
        //    super();
        //}

        private initializeHeaderedControlBehaviour(): void {
            (<any>this).registerApplyTemplateListener(() => {
                this.onHeaderPropertyChanged();
            });
        }

        private onHeaderPropertyChanged() {
            var headerElement = this.getHeaderElement();
            if (headerElement)
                Internals.setContent(headerElement, this._headerValue);
            this._onHeaderChanged(headerElement, this._headerValue);
        }

        private getHeaderElement() {
            if (this._headerElementSelector) {
                this._headerElement = this._headerElementSelector.query() as HTMLElement;
            }
            //if (!this._headerElement) {
            //    if (this.template) {
            //        this._headerElement = this.template.htmlElement.querySelector("*[template-part='header']");
            //    }
            //}
            return this._headerElement;
        }

        // protected virtual
        protected _onHeaderChanged(headerElement: HTMLElement, content: Content): void { }

        protected _setHeaderElement(headerElement: Selector | HTMLElement): void {
            if (headerElement instanceof HTMLElement) {
                this._headerElement = headerElement;
                this._headerElementSelector = null;
            } else {
                this._headerElementSelector = headerElement;
            }
        }

        public get header(): Content {
            return this._headerValue;
        }
        public set header(v: Content) {
            if (this._headerValue != v) {
                this._headerValue = v;
                this.onHeaderPropertyChanged();
                this.events.trigger("onPropertyChanged", { propertyName: "header", propertyValue: v });
            }
        }
    }

    export class HeaderedControl extends UIElement {

        constructor(template?: TemplateSource) {
            super(template);
        }

        protected initializeTemplate(templatedElement: TemplatedElement) {
            super.initializeTemplate(templatedElement);
            UIElementBehaviour.initialize(HeaderedControlBehaviour, this);
            this._setHeaderElement(templatedElement.getPart<HTMLElement>("header").element);
        }

        protected _onApplyTemplate(templatedElement: TemplatedElement) {
            super._onApplyTemplate(templatedElement);
            this.initializeTemplate(templatedElement);
        }
    }

    export interface HeaderedControl extends HeaderedControlBehaviour {
        new(template?: string | HTMLTemplateElement | HTMLElement): HeaderedControl;
    }

    Builder.applyBehaviours(HeaderedControl, [HeaderedControlBehaviour]);

    Builder.defineComponent({
        baseClass: UIElement,
        classConstructor: HeaderedControl,
        className: "Juice.HeaderedControl",
        tagName: "jui-headered-control"
    });
}