/// <reference path="contentcontrol.ts" />


namespace Juice {

    export abstract class ButtonBase extends ContentControl {

        private _isSelected = false;
        private _isToggle = false;

        constructor(template?: TemplateSource) {
            super(template);
            this.onClick = new EventSet<ButtonBase, EventArgs>(this.events, "onClick", this);
        }

        protected initializeTemplate(templatedElement: TemplatedElement) {
            super.initializeTemplate(templatedElement);
            templatedElement.rootElement.addEventListener("click", this.onButtonClick.bind(this));
        }

        protected onButtonClick(e: Event) {
            this.isSelected = !this.isSelected;
            this.events.trigger("onClick", {});
        }

        private onIsSelectedPropertyChanged() {
            if (this._isToggle) {
                if (this._isSelected) {
                    this.htmlElement.classList.add("active");
                }
                else {
                    this.htmlElement.classList.remove("active");
                }
            }
            else {
                this.htmlElement.classList.remove("active");
            }
        }


        public onClick: EventSet<ButtonBase, EventArgs>;

        public get isSelected() {
            return this._isSelected;
        }
        public set isSelected(v: boolean) {
            if (this._isSelected !== v) {
                this._isSelected = v;
                this.onIsSelectedPropertyChanged();
            }
        }

        public get isToggle() {
            return this._isToggle;
        }
        public set isToggle(v: boolean) {
            this._isToggle = v;
            if (!this._isToggle) {
                this.isSelected = false;
            }
        }

        // virtual
        public focus() {
            if (this.htmlElement instanceof HTMLButtonElement) {
                this.htmlElement.focus();
            }
        }
    }

    Builder.defineComponent({
        baseClass: Object,
        classConstructor: BindableObject,
        className: "Juice.BindableObject",
        isAbstract: true
    });


    export class Button extends ButtonBase {

        protected static readonly DefaultButtonStyles = `
    .jui-button {
        display: inline-block;
        /*padding: 4px 12px;
        border-radius: 4px;
        border: 1px solid #006ab6;
        border-bottom: 1px solid #004475;
        background-color: #006ab6;
        color: #f0f0f0;*/
        cursor: default;
        user-select: none;
    }

    /*.jui-button:hover {
        border-color: #0094ff;
        border-bottom-color: #006ab6;
        background-color: #0094ff;
        color: #fff;
        box-shadow: 0px 0px 2px 2px rgba(45, 165, 252, 0.2);
    }*/`;

        protected static readonly DefaultButtonHtmlTemplate = `
<template template-class="Juice.Button">
    <button template-part="button" class="jui-button">
        <div template-part="content" class="jui-button-content"></div>
    </button>
</template>`;

        private static readonly DefaultButtonTemplate = `<style>\n${Button.DefaultButtonStyles}\n</style>\n${Button.DefaultButtonHtmlTemplate}`;

        constructor(template?: TemplateSource) {
            super(template || Button.DefaultButtonTemplate);
            this.onClick = new EventSet<Button, EventArgs>(this.events, "onClick", this);
        }
    }

    Builder.defineComponent({
        baseClass: ButtonBase,
        classConstructor: Button,
        className: "Juice.Button",
        tagName: "jui-button"
    });



    export enum FileReadType {
        ArrayBuffer = "arrayBuffer",
        BinaryString = "binaryString",
        DataURL = "dataURL",
        Text = "text"
    }

    export class FileButton extends Button {

        protected static readonly DefaultFileButtonHtmlTemplate = `
<template template-class="Juice.FileButton">
    <button template-part="button" class="jui-button">
        <input template-part="inputFile" type="file" style="display: none" />
        <div template-part="content" class="jui-button-content"></div>
    </button>
</template>`;

        private static readonly DefaultFileButtonTemplate = `<style>\n${Button.DefaultButtonStyles}\n</style>\n${FileButton.DefaultFileButtonHtmlTemplate}`;

        private _part_inputFile: HTMLInputElement;
        private _readerHandler: (reader: FileReader) => void;

        constructor(template?: TemplateSource) {
            super(template || FileButton.DefaultFileButtonTemplate);
        }

        protected initializeTemplate(templatedElement: TemplatedElement) {
            super.initializeTemplate(templatedElement);
            this._part_inputFile = templatedElement.withPartElement<HTMLInputElement>("inputFile", (element) => {
                element.addEventListener("change", this.onHiddenFileDataChanged.bind(this));
            });
        }

        private onHiddenFileDataChanged(e: Event) {
            if (typeof this._readerHandler === "function") {
                let file = this._part_inputFile.files[0];
                let reader = new FileReader();
                reader.addEventListener("load", () => {
                    this._readerHandler(reader);
                });
                switch (this.readType) {
                    case FileReadType.ArrayBuffer: {
                        reader.readAsArrayBuffer(file);
                        break;
                    }
                    case FileReadType.BinaryString: {
                        reader.readAsBinaryString(file);
                        break;
                    }
                    case FileReadType.DataURL: {
                        reader.readAsDataURL(file);
                        break;
                    }
                    case FileReadType.Text: {
                        reader.readAsText(file);
                        break;
                    }
                    default: {
                        throw new Error("invalid read type");
                    }
                }
            }
        }

        protected onButtonClick(e: Event) {
            this._part_inputFile.click();
            super.onButtonClick(e);
        }

        public readerHandler(handler: (reader: FileReader) => void) {
            this._readerHandler = handler;
        }

        public get accept(): string {
            return this._part_inputFile.accept;
        }
        public set accept(v: string) {
            this._part_inputFile.accept = v;
        }

        public readType: FileReadType = FileReadType.Text;
    }

    Builder.defineComponent({
        baseClass: Button,
        classConstructor: FileButton,
        className: "Juice.FileButton",
        tagName: "jui-file-button"
    });



    export class LinkButton extends ButtonBase {

        protected static readonly DefaultLinkButtonStyles = `
    .jui-link-button {
        padding-left: 2px;
        padding-right: 2px;
        border-radius: 3px;
        display: inline-block;
        color: #006ab6;
        user-select: none;
    }

    .jui-link-button:hover {
        background-color: #0094ff;
        color: #fff;
        text-decoration: none;
    }`;

        protected static readonly DefaultLinkButtonHtmlTemplate = `
    <template template-class="Juice.LinkButton">
        <a template-part="content" class="jui-link-button"></a>
    </template>`;

        private static readonly DefaultLinkButtonTemplate = `<style>\n${LinkButton.DefaultLinkButtonStyles}\n</style>\n${LinkButton.DefaultLinkButtonHtmlTemplate}`;

        private _part_linkButton: HTMLAnchorElement;

        constructor(template?: TemplateSource) {
            super(template || LinkButton.DefaultLinkButtonTemplate);
            this.onClick = new EventSet<Button, EventArgs>(this.events, "onClick", this);
        }

        protected initializeTemplate(templatedElement: Juice.TemplatedElement) {
            super.initializeTemplate(templatedElement);
            this._part_linkButton = templatedElement.getPart<HTMLAnchorElement>("content").element;
            this._part_linkButton.href = "#";
        }

        public get href(): string {
            return this._part_linkButton.href;
        }
        public set href(v: string) {
            this._part_linkButton.href = v;
        }

        public get title(): string {
            return this._part_linkButton.title;
        }
        public set title(v: string) {
            this._part_linkButton.title = v;
        }
    }

    Builder.defineComponent({
        baseClass: ButtonBase,
        classConstructor: LinkButton,
        className: "Juice.LinkButton",
        tagName: "jui-link-button"
    });
}