/// <reference path="UIElement.ts" />

namespace Juice {

    export class TableView extends UIElement {

        protected static readonly DefaultTableViewStyles = `
    .jui-table-view {
        width: auto;
        white-space: nowrap;
    }

    .jui-table-view thead {
    }

    .jui-table-view th {
        white-space: nowrap;
        /*padding: 0 4px;
        font-size: 0.9em;*/
    }

    .jui-table-view td {
        white-space: nowrap;
        /*padding-left: 4px;
        padding-right: 4px;*/
    }`;

        protected static readonly DefaultTableViewHtmlTemplate = `
    <template template-class="Juice.TableView">
        <table template-part="table" class="jui-table-view">
        </table>
    </template>`;

        private static readonly DefaultTableViewTemplate = `<style>\n${TableView.DefaultTableViewStyles}\n</style>\n${TableView.DefaultTableViewHtmlTemplate}`;

        private _part_table: HTMLTableElement;

        constructor(template?: Template) {
            super(template || TableView.DefaultTableViewTemplate);
            this.onPropertyChanged.add((s, e) => {
                if (e.propertyName === "data") {
                    this.onDataPropertyChanged(e.propertyValue);
                }
            });
        }

        protected initializeTemplate(templatedElemnt: TemplatedElement) {
            super.initializeTemplate(templatedElemnt);
            this._part_table = templatedElemnt.getPart<HTMLTableElement>("table").element;
        }

        private onDataPropertyChanged(data: any) {

            this.clearTable();

            if (data === null) {
                return;
            }

            if (data instanceof Array) {
                this.loadIterable(data);
            }
            else if (data instanceof Date) {
                this.loadPrimitive(data);
            }
            else if (typeof data === "object") {
                this.loadObject(data);
            }
            else {
                this.loadPrimitive(data);
            }
        }

        private loadIterable(data: Iterable<any>) {

            let thead = document.createElement("thead");
            let tbody = document.createElement("tbody");

            if (data !== null) {

                for (let record of data) {
                    let firstEntry = true;
                    let entryType: string = null;

                    let firstItem = record;
                    if (firstItem instanceof Array) {
                        entryType = "array";
                    } else {
                        entryType = typeof firstItem;
                    }

                    if (entryType === "object") {
                        let tdIndexHead = document.createElement("th");
                        tdIndexHead.innerText = "#";
                        tdIndexHead.classList.add("index");
                        thead.appendChild(tdIndexHead);
                        for (let k of Object.keys(firstItem)) {
                            let td = document.createElement("th");
                            td.innerText = k;
                            thead.appendChild(td);
                        }

                        let index = 0;
                        for (let record of data) {
                            let tr = document.createElement("tr");
                            let tdIndex = document.createElement("td");
                            tdIndex.classList.add("index");
                            tdIndex.innerText = index.toString();
                            tr.appendChild(tdIndex);
                            for (let k of Object.keys(record)) {
                                let v = record[k];
                                let td = document.createElement("td");
                                td.innerText = v;
                                tr.appendChild(td);
                            }
                            tbody.appendChild(tr);
                            index++;
                        }
                    }
                    else if (entryType === "array") {
                        // TODO
                        let index = 0;
                        for (let record of data) {
                            let tr = document.createElement("tr");
                            let tdIndex = document.createElement("td");
                            tdIndex.classList.add("index");
                            tdIndex.innerText = index.toString();
                            tr.appendChild(tdIndex);
                            let tdValue = document.createElement("td");
                            tdValue.innerText = record.toString();
                            tr.appendChild(tdValue);
                            tbody.appendChild(tr);
                            index++;
                        }
                    }
                    else {
                        let index = 0;
                        for (let record of data) {
                            let tr = document.createElement("tr");
                            let tdIndex = document.createElement("td");
                            tdIndex.classList.add("index");
                            tdIndex.innerHTML = `<b>${index}</b>`;
                            tr.appendChild(tdIndex);
                            let tdValue = document.createElement("td");
                            tdValue.innerText = record.toString();
                            tr.appendChild(tdValue);
                            tbody.appendChild(tr);
                            index++;
                        }
                    }

                    break;
                }
            }

            this._part_table.appendChild(thead);
            this._part_table.appendChild(tbody);
        }

        private loadPrimitive(primitive: any) {
            let tr = document.createElement("tr");
            let td = document.createElement("td");
            td.innerText = primitive.toString();
            tr.appendChild(td);
            this._part_table.appendChild(tr);
        }

        private loadObject(obj: { [name: string]: any }) {

            let tbody = document.createElement("tbody");

            if (obj !== null) {
                for (let k of Object.keys(obj)) {
                    let tr = document.createElement("tr");
                    let value = obj[k];
                    if (typeof value === "function")
                        continue;
                    let tdName = document.createElement("td");
                    tdName.innerHTML = `<b>${k}</b>`;
                    tr.appendChild(tdName);
                    let tdValue = document.createElement("td");
                    tdValue.innerText = value;
                    tr.appendChild(tdValue);
                    tbody.appendChild(tr);
                }
            }

            this._part_table.appendChild(tbody);
        }

        private clearTable() {
            while (this._part_table.firstChild)
                this._part_table.firstChild.remove();
        }

        public clear() {
            this.data = null;
        }
    }

    Builder.defineComponent({
        baseClass: UIElement,
        classConstructor: TableView,
        className: "Juice.TableView",
        tagName: "jui-table-view"
    });
}